  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  height: 100vh;
}

body .wrapper:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background: #DBB57F;
  height: 100%;
}

body #main {
  flex: 1;
  position: relative;
}

