  width: 785px;
  position: relative;
  z-index: 2;
}

body .main-content {
  position: absolute;
  top: 0;
  left: 100%;
  height: calc(100% - 54px);
  background: #fff;
  transition: .3s;
  padding-top: 160px;
  width: 100%;
}

body .main-content.active {
  left: 0;
}

body .main-content:before {
  content: "";
  position: absolute;
  top: 0;
  left: -18px;
  width: 12px;
  height: 100vh;
  background: #000;
}

body .main-content:after {
  content: "";
  position: absolute;
  top: 0;
  left: -6px;
  width: 6px;
  height: 100vh;
  background: #AFAFAF;
}

