.fixed-aside {
  width: 415px;
  position: relative;
  padding-left: 20px;
  height: calc(100vh - 75px);
  z-index: 4;
}

.fixed-aside:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 1000px;
  background: #000 url("/img/left-bg.jpg") no-repeat top center/cover;
  height: 127%;
  z-index: 1;
}

.fixed-aside .fixed-inner {
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow-y: auto;
}

.fixed-aside .fixed-inner::-webkit-scrollbar {
  display: none;
}

.fixed-aside .top-details {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  transform: translateY(12px);
  margin-bottom: 120px;
}

.fixed-aside .top-details .info-list {
  display: flex;
  align-items: center;
}

.fixed-aside .top-details .info-list li {
  display: flex;
  align-items: center;
  margin-left: 26px;
}

.fixed-aside .top-details .info-list li:first-child {
  margin-left: 0;
}

.fixed-aside .top-details .info-list li svg {
  margin-right: 10px;
}

.fixed-aside .top-details .info-list a {
  display: block;
  font-family: Montserrat-Bold, Arial, Helvetica, sans-serif;
  font-size: 12px;
  color: #AFAFAF;
}

.fixed-aside .top-details .lang {
  display: flex;
  align-items: center;
  margin-left: 50px;
}

.fixed-aside .top-details .lang li {
  font-size: 24px;
  color: #4F4F4F;
  margin: 0 2px;
}

.fixed-aside .top-details .lang li.active a {
  color: #DBB57F;
}

.fixed-aside .top-details .lang a {
  font-size: 18px;
  text-transform: uppercase;
  font-family: AvenirLTStd-Book, Arial, Helvetica, sans-serif;
  color: #4F4F4F;
}

.fixed-aside .main-btn-aside {
  border-top-left-radius: 6px;
  border-bottom-left-radius: 6px;
  position: absolute;
  top: 9px;
  width: 212px;
  left: -212px;
}

.fixed-aside .aside-heading {
  font-family: BebasNeue-Regular, Arial, Helvetica, sans-serif;
  color: #DBB57F;
  font-size: 16px;
  text-transform: uppercase;
  line-height: 24px;
}

.fixed-aside .testimonials-area {
  margin-top: 17px;
  margin-bottom: 48px;
  flex: 1;
}

.fixed-aside .testimonials-area .testimonials-slider {
  margin-top: 24px;
}

.fixed-aside .testimonials-area .testimonials-slider .item p {
  font-size: 12px;
  line-height: 18px;
  color: #fff;
  font-family: Montserrat-Light, Arial, Helvetica, sans-serif;
  opacity: 1;
}

.fixed-aside .testimonials-area .testimonials-slider .item div {
  display: flex;
  align-items: center;
  font-size: 16px;
  line-height: 24px;
  margin: 5px 0 14px;
}

.fixed-aside .testimonials-area .testimonials-slider .item h2 {
  color: #fff;
  font-family: Montserrat-Bold, Arial, Helvetica, sans-serif;
}

.fixed-aside .testimonials-area .testimonials-slider .item h3 {
  color: #DBB57F;
  font-family: Montserrat-Light, Arial, Helvetica, sans-serif;
  margin-left: 10px;
}

.fixed-aside .testimonials-area .testimonials-slider .owl-dots {
  display: flex;
  justify-content: center;
}

.fixed-aside .testimonials-area .testimonials-slider .owl-dots .owl-dot {
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background: #606060;
  margin: 0 8px;
}

.fixed-aside .testimonials-area .testimonials-slider .owl-dots .owl-dot.active {
  background: #DBB57F;
}

.fixed-aside .testimonials-area .testimonials-slider .owl-dots .owl-dot:focus, .fixed-aside .testimonials-area .testimonials-slider .owl-dots .owl-dot:hover {
  outline: 0;
}

@media (max-width: 1240px) {
  .fixed-aside .top-details .lang {
    margin-left: 35px;
  }
}

@media (max-width: 991px) {
  .fixed-aside {
    max-width: 100%;
    margin: auto;
    width: 100% !important;
    height: auto;
    padding: 30px 30px 40px;
  }
  .fixed-aside .fixed-inner {
    height: auto;
  }
  .fixed-aside .testimonials-area .testimonials-slider .item div {
    justify-content: center;
    margin: 23px 0 35px;
  }
}

.main-categories li {
  height: 102px;
  border: 1px solid #DBB57F;
  margin-bottom: 10px;
  position: relative;
}

.main-categories li a {
  display: block;
  color: #fff;
  font-family: ProximaNova-Bold, Arial, Helvetica, sans-serif;
  font-size: 19px;
  line-height: 17px;
  text-shadow: 2px 2px 3px black;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 2;
  text-transform: uppercase;
}

.main-categories li:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(112, 112, 112, 0.3);
  z-index: 1;
  transition: all .3s;
}

.main-categories li:nth-child(3) {
  background: url("/img/showroom.jpg") no-repeat center center;
}

.main-categories li:hover:before {
  background: rgba(219, 181, 127, 0.6);
}

@media (max-width: 991px) {
  .main-categories {
    max-width: 400px;
    margin: auto;
    width: 100%;
  }
}

.contact-popup-formular {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 6;
  opacity: 0;
  visibility: hidden;
  transition: all .3s;
  transform: scale(0);
  padding: 20px;
  background: rgba(0, 0, 0, 0.7);
}

.contact-popup-formular.active {
  opacity: 1;
  visibility: visible;
  transform: scale(1);
}

.contact-popup-formular .popup-inner {
  background: #fff;
  border-radius: 7px;
  width: 650px;
  overflow-y: auto;
}

.contact-popup-formular .pop-heading {
  background: #DBB57F;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
}

.contact-popup-formular .pop-heading i {
  color: #fff;
}

.contact-popup-formular .pop-heading .close-pop {
  cursor: pointer;
}

.contact-popup-formular .popup-content {
  padding: 30px;
  width: 95%;
  margin: auto;
  background: #fff;
}

.contact-popup-formular p strong {
  font-family: Montserrat-Bold, Arial, Helvetica, sans-serif;
}

.contact-popup-formular form dl {
  display: flex;
  height: 40px;
}

.contact-popup-formular form dl :is(dt, dd) {
  display: block;
}

.contact-popup-formular form dl dt {
  width: 40%;
  color: #DBB57F;
}

.contact-popup-formular form dl dd {
  width: 60%;
}

.contact-popup-formular form dl dd input {
  font-size: 14px;
}

.contact-popup-formular form dl.for-textarea {
  height: 65px;
}

.contact-popup-formular .small-title {
  color: #DBB57F;
  font-family: BebasNeue-Regular, Arial, Helvetica, sans-serif;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 24px;
}

.contact-popup-formular .to-right div {
  display: flex;
  align-items: center;
}

.contact-popup-formular .to-right div input {
  width: auto;
  margin-right: 5px;
  margin-bottom: 0;
}

.contact-popup-formular .to-right div label {
  line-height: 23px;
}

@media (max-width: 767px) {
  .contact-popup-formular {
    align-items: flex-start;
  }
  .contact-popup-formular .popup-content {
    width: 100%;
    padding: 20px;
  }
  .contact-popup-formular .popup-inner {
    overflow-y: auto;
    height: calc(100vh - 40px);
  }
  .contact-popup-formular form dl {
    flex-direction: column;
    height: auto;
  }
  .contact-popup-formular form dl dt {
    font-size: 13px;
    width: 100%;
  }
  .contact-popup-formular form dl dd {
    width: 100%;
  }
  .contact-popup-formular form dl dd input {
    margin-bottom: 10px;
  }
  .contact-popup-formular .to-right {
    margin-top: 30px !important;
  }
  .contact-popup-formular .to-right div {
    align-items: flex-start;
  }
  .contact-popup-formular .to-right div label {
    font-size: 14px;
  }
}

