.sr-only {
  border: 0;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  width: auto;
}

header {
  position: fixed;
  top: 53px;
  left: 50%;
  transform: translateX(-50%);
  width: 1200px;
  z-index: 6;
}

header .top-header {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  transform: translateY(12px);
}

header .top-header .info-list {
  display: flex;
  align-items: center;
}

header .top-header .info-list li {
  display: flex;
  align-items: center;
  margin-left: 26px;
}

header .top-header .info-list li svg {
  margin-right: 10px;
}

header .top-header .info-list a {
  display: block;
  font-family: Montserrat-Bold, Arial, Helvetica, sans-serif;
  font-size: 12px;
  color: #AFAFAF;
}

header .bottom-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 34px;
  transition: all .3s;
}

header .bottom-header .logo {
  position: relative;
  height: 75px;
}

header .bottom-header .logo svg {
  position: absolute;
  top: -24px;
  left: 0;
}

header .info-list {
  display: flex;
  align-items: center;
}

header .navigation {
  width: calc(100% - 142px);
}

header nav {
  width: 100%;
}

header nav > ul {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  background: #DBB57F;
  border: 3px solid #fff;
  height: 75px;
  border-top-left-radius: 37px;
  border-bottom-left-radius: 37px;
  padding-right: 15px;
  width: 100%;
}

header nav > ul > li {
  margin: 0 16px;
  position: relative;
  display: flex;
  align-items: center;
}

header nav > ul > li:hover .dropdown {
  opacity: 1;
  visibility: visible;
  top: 100%;
}

header nav > ul > li:hover > a {
  color: #000000;
}

header nav > ul > li i {
  font-size: 12px;
  transform: translateX(3px);
}

header nav > ul > li > a {
  font-family: BebasNeue-Regular, Arial, Helvetica, sans-serif;
  color: #fff;
  text-transform: uppercase;
  font-size: 20px;
  line-height: 23px;
  transition: all .3s;
}

header nav > ul > li > a.active {
  color: var(--black-color);
}

header nav .dropdown {
  position: absolute;
  top: 40px;
  padding-top: 15px;
  left: 0;
  width: 150px;
  opacity: 0;
  visibility: hidden;
  transition: all .3s;
}

header nav .dropdown ul {
  background: #DBB57F;
  border: 1px solid #000000;
  position: relative;
  padding: 15px 0;
}

header nav .dropdown ul:before {
  content: "";
  position: absolute;
  top: -11px;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 0;
  border-left: 9px solid transparent;
  border-right: 9px solid transparent;
  border-bottom: 11px solid #000000;
}

header nav .dropdown ul li {
  transition: all .3s;
}

header nav .dropdown ul li:hover {
  background: #000000;
}

header nav .dropdown ul li:hover a {
  color: #fff;
}

header nav .dropdown ul a {
  font-family: BebasNeue-Regular, Arial, Helvetica, sans-serif;
  color: #fff;
  text-transform: uppercase;
  font-size: 18px;
  line-height: 24px;
  padding: 3px 16px 0;
  transition: all .3s;
  display: block;
}

header .lang {
  display: flex;
  align-items: center;
  margin-left: 50px;
}

header .lang li {
  font-size: 24px;
  color: #4F4F4F;
  margin: 0 2px;
}

header .lang li.active a {
  color: #DBB57F;
}

header .lang a {
  font-size: 18px;
  text-transform: uppercase;
  font-family: AvenirLTStd-Book, Arial, Helvetica, sans-serif;
  color: #4F4F4F;
}

@media (max-width: 1240px) {
  header {
    width: 100%;
    padding-right: 20px;
  }
  header .navigation {
    width: calc(100% - 130px);
  }
  header .bottom-header {
    padding-left: 29px;
  }
  header nav .close-menu {
    position: absolute;
    top: 15px;
    right: 15px;
    color: #fff;
    font-size: 25px;
  }
  header nav > ul > li {
    margin: 0 13px;
  }
  header nav > ul > li > a {
    font-size: 18px;
    line-height: 21px;
  }
}

@media (max-width: 1055px) {
  header nav > ul > li {
    margin: 0 10px;
  }
}

@media (max-width: 991px) {
  header {
    padding-right: 0;
    top: 0;
  }
  header.fix .bottom-header {
    background: #fff;
    margin-top: 0;
    box-shadow: 1px 0px 7px 1px rgba(0, 0, 0, 0.2);
    padding-top: 5px !important;
    padding-bottom: 5px !important;
  }
  header .top-header {
    background: #1E1E1E;
    transform: translateY(0);
    height: 84px;
    padding: 0 20px;
    justify-content: space-between;
  }
  header .top-header .info-list {
    display: block;
  }
  header .top-header .info-list li:nth-child(2) {
    margin-top: 15px;
  }
  header .navigation {
    width: auto;
  }
  header nav {
    position: fixed;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100vh;
    z-index: 6;
    background: #000000;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 50px 0 30px;
    opacity: 0;
    visibility: hidden;
    transition: all .3s;
  }
  header nav.active {
    left: 0;
    opacity: 1;
    visibility: visible;
  }
  header nav > ul {
    display: block;
    height: calc(100% - 60px);
    background: transparent;
    border: 0 none;
    border-radius: 0;
    padding-right: 0;
    overflow-y: auto;
  }
  header nav > ul > li {
    width: 100%;
    padding: 15px 10px;
    border-top: 1px solid rgba(255, 255, 255, 0.2);
    position: relative;
    flex-wrap: wrap;
    margin: 0;
  }
  header nav > ul > li:first-child {
    border-top: 0;
  }
  header nav > ul > li .fa {
    color: #fff;
    width: 30%;
    text-align: center;
    height: 27px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 14px;
    right: 15px;
  }
  header nav > ul > li a {
    display: block;
    width: 100%;
    font-size: 25px;
    line-height: 27px;
    color: #fff !important;
  }
  header nav .dropdown {
    display: none;
    opacity: 1;
    visibility: visible;
    position: relative;
    top: unset;
    width: 100%;
    transition: unset;
  }
  header nav .dropdown i {
    display: none;
  }
  header nav .dropdown ul a {
    font-size: 23px;
    line-height: 31px;
  }
  header .lang {
    margin-left: 0;
  }
  header .bottom-header {
    padding: 0 26px !important;
    margin-top: 17px;
  }
  header .bottom-header .logo {
    height: auto;
  }
  header .bottom-header .logo svg {
    width: 93px;
    height: 93px;
    position: relative;
    top: unset;
    transition: all .3s;
  }
}

@media (max-width: 767px) {
  header {
    position: absolute;
  }
  header .hamburger span {
    transition: all .3s;
  }
  header .bottom-header .logo {
    transform: translateX(-10px);
  }
  header.fix {
    position: fixed;
  }
  header.fix .top-header {
    display: none !important;
  }
  header.fix .bottom-header {
    background: #fff;
    margin-top: 0;
  }
  header.fix .bottom-header .logo svg {
    width: 68px;
    height: 68px;
  }
  header.fix .hamburger span {
    height: 4px;
    margin-bottom: 6px;
  }
  header .top-header .info-list li {
    margin-left: 0;
  }
  header .contact-formular {
    font-size: 14px;
  }
}

@media (max-width: 400px) {
  header #header .info-list li {
    font-size: 11px;
  }
  header #header .info-list li i {
    margin-right: 5px;
  }
  header #header .info-list li:last-child {
    margin-left: 15px;
  }
}

